.sidebar{
  position: fixed;
  box-sizing: border-box;
  width: 269px;
  height: 100vh;
  left: 0;
  top: 0;
  background: $blue;
  z-index: 100;
  overflow: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  .flex-block{
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 5.05vh 48px;
  }
  .logo{
    width: 140px;
    display: block;
    svg{
      width: 100%;
    }
  }
  .langs{
    margin-top: 8px;
    a{
      font-size: 15px;
      line-height: 135%;
      color: $gray;
      +a{
        margin-left: 10px;
      }
      &:hover{
        color: $white;
      }
      &.active{
        color: $green;
      }
    }
  }
  .profile-menu{
    margin-top: 6.73vh;
    >div{
      +div{
        margin-top: 25px;
      }
      a{
        display: flex;
        align-items: center;
        font-size: 15px;
        line-height: 150%;
        color: $gray;
        .img{
          flex-shrink: 0;
          width: 21px;
          height: 20px;
          display: block;
          margin-right: 16px;
          svg{
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
        &:hover{
          color: $white;
          .img{
            svg *{
              fill: $white;
            }
          }
        }
        &.active{
          color: $green;
          .img{
            svg *{
              fill: $green;
            }
          }
        }
      }
    }
  }
  .phones{
    margin-top: 6.73vh;
    >div{
      a{
        color: #ffffff;
        font-size: 15px;
        line-height: 200%;
        &:hover{
          color: $green;
        }
      }
    }
  }
  .copy{
    font-size: 15px;
    line-height: 150%;
    color: #8599B0;
    margin-top: 6.73vh;
  }
}