@import url(partials/reset.css);

@import url(partials/fonts.css);

@import url(partials/jquery.fancybox.min.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

body {
  color: #3b3b3b;
  font-family: "SF Pro Display", sans-serif;
  font-size: 15px;
  margin: 0;
  background: #ffffff;
}

.container {
  margin: 0 auto;
  box-sizing: border-box;
}

.info-block {
  background: #FFFCE1;
  width: 100%;
  box-sizing: border-box;
  padding: 16px 16px 24px;
  display: flex;
  align-items: flex-start;
}

.info-block .img {
  width: 26px;
  height: 27px;
  display: block;
  margin-right: 14px;
  background: url("../img/info.svg") center center no-repeat;
  background-size: contain;
  flex-shrink: 0;
}

.info-block .text {
  font-size: 13px;
  line-height: 135%;
  color: #000000;
}

.section-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 135%;
  text-transform: uppercase;
  color: #3DA163;
  margin-bottom: 40px;
}

.input-block {
  width: 100%;
  max-width: 100%;
  position: relative;
}

.input-block span {
  color: #94959A;
  font-size: 15px;
  text-align: left;
  width: 100%;
  height: 100%;
  position: absolute;
  pointer-events: none;
  left: 0;
  top: 0;
  transition: 0.2s ease all;
  box-sizing: border-box;
  line-height: 0.8;
  padding-left: 16px;
  padding-top: 18px;
}

.input-block input {
  background: #ffffff;
  font-family: "SF Pro Display", sans-serif;
  font-weight: 400;
  width: 100%;
  border: 1px solid #C6C8D4;
  box-sizing: border-box;
  border-radius: 1px;
  font-size: 15px;
  line-height: 18px;
  color: #000000;
  padding-left: 15px;
  padding-right: 36px;
  padding-top: 20px;
  height: 48px;
}

.input-block input.datepicker {
  background-image: url("../img/date.svg");
  background-repeat: no-repeat;
  background-position: right 16px center;
  background-size: 21px auto;
}

.input-block input:focus {
  outline: none;
  border: 1px solid #8F929F;
}

.input-block input:focus ~ span,
.input-block input:not(:focus):valid ~ span {
  padding-top: 9px !important;
  font-size: 11px;
}

.input-block .hit {
  width: 20px;
  height: 20px;
  background: #FFFCE1;
  position: absolute;
  right: 16px;
  top: 50%;
  margin-top: -10px;
  text-align: center;
  color: #0C3362;
  font-size: 13px;
  line-height: 20px;
  cursor: default;
  border-radius: 100%;
}

.input-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -30px;
  margin-top: -24px;
}

.input-row + .input-row {
  margin-top: 0;
}

.input-row .input-block {
  flex: 1 0 150px;
  margin-left: 30px;
  margin-top: 24px;
}

.form-style .form-section + .form-section {
  margin-top: 64px;
}

.main {
  padding-left: 269px;
  display: flex;
}

.form-col {
  width: 540px;
  max-width: 100%;
}

.form-col form {
  margin-top: 36px;
}

.sidebar {
  position: fixed;
  box-sizing: border-box;
  width: 269px;
  height: 100vh;
  left: 0;
  top: 0;
  background: #0C3362;
  z-index: 100;
  overflow: auto;
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar .flex-block {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 5.05vh 48px;
}

.sidebar .logo {
  width: 140px;
  display: block;
}

.sidebar .logo svg {
  width: 100%;
}

.sidebar .langs {
  margin-top: 8px;
}

.sidebar .langs a {
  font-size: 15px;
  line-height: 135%;
  color: #9DAFC3;
}

.sidebar .langs a + a {
  margin-left: 10px;
}

.sidebar .langs a:hover {
  color: #ffffff;
}

.sidebar .langs a.active {
  color: #3DA163;
}

.sidebar .profile-menu {
  margin-top: 6.73vh;
}

.sidebar .profile-menu > div + div {
  margin-top: 25px;
}

.sidebar .profile-menu > div a {
  display: flex;
  align-items: center;
  font-size: 15px;
  line-height: 150%;
  color: #9DAFC3;
}

.sidebar .profile-menu > div a .img {
  flex-shrink: 0;
  width: 21px;
  height: 20px;
  display: block;
  margin-right: 16px;
}

.sidebar .profile-menu > div a .img svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.sidebar .profile-menu > div a:hover {
  color: #ffffff;
}

.sidebar .profile-menu > div a:hover .img svg * {
  fill: #ffffff;
}

.sidebar .profile-menu > div a.active {
  color: #3DA163;
}

.sidebar .profile-menu > div a.active .img svg * {
  fill: #3DA163;
}

.sidebar .phones {
  margin-top: 6.73vh;
}

.sidebar .phones > div a {
  color: #ffffff;
  font-size: 15px;
  line-height: 200%;
}

.sidebar .phones > div a:hover {
  color: #3DA163;
}

.sidebar .copy {
  font-size: 15px;
  line-height: 150%;
  color: #8599B0;
  margin-top: 6.73vh;
}